import Vue from 'vue'
import VueRouter from 'vue-router'

// 二级路由
import User from '@/views/layout/user'
import Home from '@/views/layout/home'
import Cart from '@/views/layout/cart'
import Categroy from '@/views/layout/categroy'
import store from '@/store'

const Layout = () => import('@/views/layout')
const Login = () => import('@/views/login')
const Myordern = () => import('@/views/myorder')
const Pay = () => import('@/views/pay')
const Prodetail = () => import('@/views/prodetail')
const Search = () => import('@/views/search')
const SearchList = () => import('@/views/search/list')
const Address = () => import('@/views/address')
const Updata = () => import('@/views/address/updata')
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [
      { path: '/user', component: User },
      { path: '/home', component: Home },
      { path: '/cart', component: Cart },
      { path: '/categroy', component: Categroy }
    ]
  },
  { path: '/login', component: Login },
  { path: '/myorder', component: Myordern },
  { path: '/pay', component: Pay },
  { path: '/prodetail/:id', component: Prodetail },
  { path: '/search', component: Search },
  { path: '/searchlist', component: SearchList },
  { path: '/address', component: Address },
  { path: '/address/updata', component: Updata }
]

const router = new VueRouter({
  routes
})
const authUrl = ['/pay', '/myorder']
router.beforeEach((to, from, next) => {
  const token = store.getters.token
  if (!authUrl.includes(to.path)) {
    next()
    return
  }

  if (token) {
    next()
  } else {
    next('/login')
  }
})
export default router
