import { getCartList, changeCount, delSelect } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state() {
    return {
      cartList: []
    }
  },
  mutations: {
    setCartList(state, newList) {
      state.cartList = newList
    },
    // 反选
    toggleCheck(state, goodsId) {
      const goods = state.cartList.find((item) => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    // 全选
    toggleAllCheck(state, flag) {
      state.cartList.forEach((item) => (item.isChecked = flag))
    },
    changeCount(state, obj) {
      const goods = state.cartList.find((item) => item.goods_id === obj.goodsId)
      goods.goods_num = obj.value
    }
  },
  actions: {
    async getCartActions(context) {
      const { data } = await getCartList()
      data.list.forEach((item) => {
        item.isChecked = true
      })
      context.commit('setCartList', data.list)
    },
    async changeCountAction(context, obj) {
      await changeCount(obj.goodsId, obj.value, obj.skuId)
      context.commit('changeCount', obj)
    },
    async delSelect(context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map((item) => item.id)
      await delSelect(cartIds)
      Toast('删除成功')
      context.dispatch('getCartActions')
    }
  },
  getters: {
    // 求所有商品的总数
    cartTotal(state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中商品项
    selCartList(state) {
      return state.cartList.filter((item) => item.isChecked)
    },
    // 选中总数
    selCount(state, getters) {
      return getters.selCartList.reduce((sum, item) => item.goods_num + sum, 0)
    },
    // 选中总价
    selPrice(state, getters) {
      return getters.selCartList
        .reduce(
          (sum, item) => item.goods_num * item.goods.goods_price_min + sum,
          0
        )
        .toFixed(2)
    },
    // 全选
    isleAllCheck(state) {
      return state.cartList.every((item) => item.isChecked === true)
    }
  }
}
